
<template>
    <div class="three-display" :style="{width: `${width}px`, height: `${height}px`}">
        <div @click="mouseClick" id="three-canvas" class="three-canvas" ref="three-canvas"></div>
    </div>
</template>

<script>
import Stats from '../../../node_modules/three/examples/jsm/libs/stats.module'
import { OrbitControls } from '../../../node_modules/three/examples/jsm/controls/OrbitControls'
import { TransformControls } from '../../../node_modules/three/examples/jsm/controls/TransformControls'
export default {
  props: {
    width: Number, // 
    height: Number // 
  },
  data () {
    return {
      renderer: '', // 
      scene: '', // 
      camera: '', // 
      stats: '', // 
      raycaster: '', // 
      mouse: '', // 
      showUpdateTranformControls: false, // 
      lightBox: '',
      helperBox: '',
      clock: '', // 
      orbitControls: '', // 
      transformControls: '', // 
      animationFrame: '' // 
    }
  },
  methods: {
    // 
    init () {
      this.initRender()
      this.initCamera()
      this.initLight()
      this.initHelper()
      this.initStats()
      this.initOrbitControls()
      this.initTranformControls()
      this.initRaycasterAndMouse()
      this.updateRenderer()
    },
    // 
    initRender () {
      this.renderer.setSize(this.width, this.height)
      // this.renderer.shadowMap.enabled = true // 
      // this.renderer.shadowMap.type = THREE.PCFSoftShadowMap // 
      this.$refs['three-canvas'].appendChild(this.renderer.domElement)
      this.renderer.setClearColor('rgb(15, 1, 25)')
    },
    // 
    initLight () {
      this.lightBox.directionalLight.position.set(300, 500, 300)
      this.scene.add(this.lightBox.ambientLight)
      this.scene.add(this.lightBox.directionalLight)
    },
    // 
    initCamera () {
      this.camera.position.x = 400
      this.camera.position.y = 400
      this.camera.position.z = 400
      this.camera.up.x = 0
      this.camera.up.y = 1
      this.camera.up.z = 0
      this.camera.lookAt(new THREE.Vector3(0, 0, 0))
    },
    // 
    initHelper () {
      this.scene.add(this.helperBox.axesHelper.helper)
      this.scene.add(this.helperBox.gridHelper.helper)
    },
    // 
    initStats () {
      this.stats.setMode(0)
      this.stats.domElement.id = 'three-stats'
      this.stats.domElement.style.position = 'absolute'
      this.stats.domElement.style.left = 'unset'
      this.stats.domElement.style.right = '0px'
      this.stats.domElement.style.top = '0px'
      document.body.appendChild(this.stats.domElement)
    },
    // 
    initRaycasterAndMouse () {
      this.raycaster = new THREE.Raycaster()
      this.mouse = new THREE.Vector2()
    },
    // 
    mouseClick (event) {
      // x  y  (-1 to +1)
      this.mouse.x = (event.offsetX / this.width) * 2 - 1
      this.mouse.y = -(event.offsetY / this.height) * 2 + 1
      this.showUpdateTranformControls = true
    },
    // 
    initOrbitControls () {
      this.orbitControls = new OrbitControls(this.camera, this.renderer.domElement)
      this.orbitControls.autoRotate = false
      this.orbitControls.enablePan = true
      this.orbitControls.mouseButtons = {
        LEFT: '',
        MIDDLE: THREE.MOUSE.DOLLY,
        RIGHT: THREE.MOUSE.ROTATE
      }
      this.orbitControls.keys = {
        LEFT: 65, // a
        UP: 87, // w
        RIGHT: 68, // d
        BOTTOM: 83 // s
      }
    },
    // 
    initTranformControls () {
      this.transformControls = new TransformControls(this.camera, this.renderer.domElement)
    },
    // 
    raycasterOperate () {
      // 
      this.raycaster.setFromCamera(this.mouse, this.camera)
      // 
      const intersects = this.raycaster.intersectObjects(this.scene.children)
      // let hasMesh = false
      console.log(intersects)
      if (intersects[0]) {
        // axis,
        for (let num = 0; num < intersects.length; num += 1) {
          if (intersects[num].object.type !== 'AxesHelper' && intersects[num].object.type !== 'GridHelper') {
            // helper
            if (intersects[num].object.type === 'PointLightHelper') {
              this.transformControls.attach(intersects[num].object.light)
              this.scene.add(this.transformControls)
            } else {
              this.transformControls.attach(intersects[num].object)
              this.scene.add(this.transformControls)
            }

            // hasMesh = true
            break
          }
        }
      }
      // if (!hasMesh) {
      //   this.scene.remove(this.transformControls)
      // }
      this.showUpdateTranformControls = false
    },
    // 
    animation () {
    },
    // 
    updateRenderer () {
      const delta = this.clock.getDelta()
      this.orbitControls.update(delta)
      if (this.showUpdateTranformControls) {
        this.raycasterOperate()
      }
      this.renderer.render(this.scene, this.camera)
      this.stats.update()
      this.animationFrame = requestAnimationFrame(this.updateRenderer)
    },
    // 
    clearObjCache (obj) {
      obj.geometry.dispose()
      obj.material.dispose()
    },
    // 
    clearCache () {
      // 
      this.renderer.clear(true, true, true)
      this.renderer.dispose()
      this.renderer.forceContextLoss()
      this.renderer.domElement = null
      this.clearObjCache(this.objBox.obj1)
      // 
      this.scene.dispose()
    },
    // 
    resetRenderer () {
      this.renderer.setSize(this.width, this.height)
      this.camera.aspect = this.width / this.height
      this.camera.updateProjectionMatrix()
    },
    // 
    addGeometry (setting) {
      const geometry = eval(setting.init)
      const material = new THREE.MeshLambertMaterial({ color: 'rgb(115, 30, 150)' })
      const mesh = new THREE.Mesh(geometry, material)
      mesh.name = `${setting.name}${mesh.id}`
      // mesh.castShadow = true
      // mesh.receiveShadow = true
      this.scene.add(mesh)
      this.transformControls.attach(mesh)
      this.scene.add(this.transformControls)
    },
    // 
    addLight (setting) {
      let light = ''
      let helper = ''
      console.log(setting.name)
      const color = 'rgb(255, 255, 255)'
      if (setting.name === 'PointLight') {
        light = new THREE.PointLight(color, 3, 400, 1)
        helper = new THREE.PointLightHelper(light, 50)
      } else if (setting.name === 'DirectionalLight') {
        light = new THREE.DirectionalLight(color, 1)
        helper = new THREE.DirectionalLightHelper(light, 50)
      } else if (setting.name === 'HemisphereLight') {
        light = new THREE.HemisphereLight('blue', 'red', 1)
        helper = new THREE.HemisphereLightHelper(light, 500)
      } else if (setting.name === 'SpotLight') {
        light = new THREE.SpotLight(color, 1, 300, Math.PI / 180 * 45)
        helper = new THREE.SpotLightHelper(light)
        light.position.set(0, 300, 0)
      }
      // light.castShadow = true
      this.scene.add(light)
      this.scene.add(helper)
      this.transformControls.attach(light)
      this.scene.add(this.transformControls)
    }
  },
  watch: {
    // 
    width (newValue, oldValue) {
      this.resetRenderer()
    },
    height (newValue, oldValue) {
      this.resetRenderer()
    }
  },
  created () {
    this.$bus.$on('addGeometry', this.addGeometry)
    this.$bus.$on('addLight', this.addLight)
  },
  beforeMount () {
    this.renderer = new THREE.WebGLRenderer({ antialias: true }) // 
    this.scene = new THREE.Scene() // 
    this.camera = new THREE.PerspectiveCamera(45, this.width / this.height, 1, 10000) // 
    this.stats = new Stats()
    this.lightBox = {
      ambientLight: new THREE.AmbientLight('rgb(255, 255, 255)'), // 
      directionalLight: new THREE.DirectionalLight('rgb(255, 255, 255)', 1)
    }
    this.helperBox = {
      axesHelper: { helper: new THREE.AxesHelper(10000) }, // 
      gridHelper: { helper: new THREE.GridHelper(1500, 30, 'white', 'rgb(150, 150, 150)') } // 
    }
    this.objBox = {
      obj1: '' // 
    }
    this.clock = new THREE.Clock() // 
  },
  mounted () {
    this.init()
    window.onkeydown = (event) => {
      switch (event.keyCode) {
        case 81: // Q
          this.transformControls.setSpace(this.transformControls.space === 'local' ? 'world' : 'local')
          break
        case 16: // Shift
          this.transformControls.setTranslationSnap(100)
          this.transformControls.setRotationSnap(THREE.MathUtils.degToRad(15))
          this.transformControls.setScaleSnap(0.25)
          break
        case 69: // e
          this.transformControls.setMode('translate')
          break
        case 82: // r
          this.transformControls.setMode('rotate')
          break
        case 84: // t
          this.transformControls.setMode('scale')
          break
        case 187:
        case 107: // +, =, num+
          this.transformControls.setSize(this.transformControls.size + 0.1)
          break
        case 189:
        case 109: // -, _, num-
          this.transformControls.setSize(Math.max(this.transformControls.size - 0.1, 0.1))
          break
        case 88: // X
          this.transformControls.showX = !this.transformControls.showX
          break
        case 89: // Y
          this.transformControls.showY = !this.transformControls.showY
          break
        case 90: // Z
          this.transformControls.showZ = !this.transformControls.showZ
          break
        case 32: // Spacebar
          this.transformControls.enabled = !this.transformControls.enabled
          break
      }
    }
  },
  // 
  beforeDestroy () {
    document.body.removeChild(document.getElementById('three-stats'))
    window.onkeydown = null
    this.renderer = null
    this.scene = null
    this.camera = null
    this.lightBox = null
    this.helperBox = null
    this.objBox = null
    this.clock = null
    this.orbitControls = null
    this.transformControls = null
    cancelAnimationFrame(this.animationFrame)
  }
}
</script>

<style lang="less" scoped>
.three-display {
  .absolutePosition();
}
</style>
